.header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  width: 100%;
  height: 10.4vh;
  padding: 2.5% 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.header h1 {
  color: #2e2e2e;
  letter-spacing: 8px;
}

.header h1 {
  font-family: "Work Sans", sans-serif;
  font-weight: 300;
}

.firstName {
  margin-right: 30px;
}

.header h1 .firstName {
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
}

.currentPos {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e7e8ea;
  width: 100%;
  height: 2.3em;
}

.currentPos h2 {
  color: #2e2e2e;
  font-size: 14px;
  letter-spacing: 3px;
}

.positionHeader {
  font-weight: 300;
  font-family: "Work Sans", sans-serif;
}
