* {
  margin: 0;
  padding: 0;
}

.personalForm {
  display: flex;
  flex-direction: column;
  background-color: #1c1c1c;
  padding: 3.75% 5% 4.25% 5%;
  border-radius: 12px;
}

.persForm {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.personalForm h2 {
  margin-bottom: 4.25%;
  color: white;
  font-family: "Kumbh Sans", sans-serif;
}

.contact {
  display: flex;
  gap: 10px;
  margin-bottom: 5%;
}

.name {
  display: flex;
  gap: 10px;
}

input,
textarea {
  width: 100%;
  box-sizing: border-box;
  padding: 1.5% 0;
  padding-left: 10px;
  border-radius: 10px;
  background-color: #292929;
  border: 1px transparent;
  font-family: "Poppins", sans-serif;
  transition: all 0.3s, transform 0.2s, -webkit-transform 0.2s;
}

input {
  color: #a6a6a6;
}

input:focus {
  color: #fff;
}

textarea {
  width: 100%;
  height: 100px;
  resize: none;
}

input:hover,
textarea:hover {
  background-color: #2d2d2d;
}

input:focus,
textarea:focus {
  background-color: #333;
  outline: none;
}

input:active,
textarea:active {
  transform: scale(0.98);
}
